.heading1 {
  font-family: roboto;
  color: #333333;
  width: 30vw;
}
.inital-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.button-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.Reset {
  color: #ffffff;
  background-color: #eaa800;
  border-width: 0px;
  height: 30px;
  border-radius: 5px;
  width: 80px;
}
.Stop {
  font-family: roboto;
  color: #ffffff;
  background-color: #ef0d36;
  border-width: 0px;
  height: 30px;
  border-radius: 5px;
  width: 80px;
}
.Start {
  font-family: roboto;
  color: #ffffff;
  background-color: #1db05f;
  border-width: 0px;
  height: 30px;
  border-radius: 5px;
  width: 80px;
}
.sub-heading {
  font-family: roboto;
  font-size: 38px;
  text-align: center;
}
.timer-heading {
  font-family: roboto;
  color: #333333;
  font-weight: bold;
}
.sub-timer-container {
  display: flex;
  flex-direction: row;
  width: 70px;
  margin-left: 110px;
  justify-content: space-between;
}
.image {
  height: 20px;
  width: 20px;
  margin-top: 17px;
}
.timer-container {
  box-shadow: 0px 4px 16px 0px #bfbfbf;
  width: 300px;
  border-radius: 6px;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;
}
